
import { defineComponent, PropType } from 'vue';
import ICountryDetail from '@/types/Detail';


export default defineComponent({
  name: 'DetailCard',
  props: {
    countryDetail: {
      required: true,
      type: Object as PropType<ICountryDetail>
    },
  },
  methods:{
      setLink(str:string){
          return { name: 'About', params: { id: str}}
      }
  }
});
